




























































import { Component, Vue, Prop } from 'vue-property-decorator';
import {
  State,
  Getter,
  Action,
  namespace
} from 'vuex-class';
import { AlbumDetails } from '../models/album.details';
import { Type } from '../models/type';

const musicModule = namespace('Music');

@Component({
  name: 'Music'
})
export default class Music extends Vue {

  // props
  @Prop({type: String})
  private id!: string;

  @musicModule.State
  private loading!: boolean;
  @musicModule.Getter
  private getAlbum: any;
  @musicModule.Getter
  private getSongs: any;
  @musicModule.Action
  private getAlbumDetails: any;

  // computed
  get noResults() {
    return !this.getAlbum.artistName;
  }

  // lifecycle phases
  public async mounted() {
    await this.getAlbumDetails(this.id);
  }
}
