



























import { Component, Vue, Prop } from 'vue-property-decorator';
import { Type } from '../models/type';

@Component({
  name: 'AlbumCard'
})
export default class AlbumCard extends Vue {

  @Prop({type: Object})
  private item!: Type;

  public singleMusic(id: number) {
    this.$router.push('/music/' + id);
  }
}
